import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import {FormsModule} from '@angular/Forms';
import { RouterModule, Routes } from '@angular/router';

import { AppComponent } from './app.component';
import {AppaComponent} from './app.A';
import {AppbComponent} from './app.B';
import {AppcComponent} from './app.C';

const appRoutes: Routes = [
  { path: 'a', component: AppaComponent },
  { path: 'b', component: AppbComponent },
  { path: 'c', component: AppcComponent },
  { path: '',
    pathMatch: 'full',
    component: AppaComponent
  },
  { path: '**', component: AppaComponent }
];


@NgModule({
  declarations: [
    AppComponent,
    AppaComponent,
    AppbComponent,
    AppcComponent
      ],
  imports: [
    BrowserModule,
    RouterModule.forRoot(
      appRoutes,
      {enableTracing: true,
        useHash: true
      }
    )
    ],
  providers: [],
  bootstrap: [AppComponent]
})
export class AppModule { }



